import React from "react";
import { Theme } from "@faharmony/theme";
import { Preloader } from "@faharmony/core";
import { setTheme, getCurrentDarkModePreference } from "@faharmony/theme";
import Div100vh from "react-div-100vh";

// Pre loading screen
export const Loader = () => {
  setTheme({ enableDarkMode: getCurrentDarkModePreference() });
  return (
    <Div100vh
      id="full-height-wrapper"
      style={{ width: "100vw", background: Theme.colorBackground("base") }}
    >
      <Preloader />
    </Div100vh>
  );
};

export default Loader;
