import {
  useEffect,
  useRef,
  useState,
  useCallback,
  RefObject,
  CSSProperties
} from "react";
import useMountedState from "../state/useMountedState";

export const isClient = typeof window === "object";

export const on = (obj: any, ...args: any[]) => obj.addEventListener(...args);

export const off = (obj: any, ...args: any[]) =>
  obj.removeEventListener(...args);

const useSetState = <T extends object>(
  initialState: T = {} as T
): [T, (patch: Partial<T> | ((prevState: T) => Partial<T>)) => void] => {
  const [state, set] = useState<T>(initialState);
  const setState = useCallback(
    patch => {
      set(prevState =>
        Object.assign(
          {},
          prevState,
          patch instanceof Function ? patch(prevState) : patch
        )
      );
    },
    [set]
  );

  return [state, setState];
};

export interface State {
  isSliding: boolean;
  value: number;
}

export interface Options {
  onScrub: (value: number) => void;
  onScrubStart: () => void;
  onScrubStop: () => void;
  reverse: boolean;
  styles: boolean | CSSProperties;
  vertical?: boolean;
  default?: number;
}

const noop = () => null;

/**
 * Hook to provide slide behavior over any HTML element.
 * @author Vadim Dalecky <> https://github.com/streamich/
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @param ref Target slidable element
 */
export const useSlider = (
  ref: RefObject<HTMLElement>,
  options: Partial<Options> = {}
): State => {
  const isMounted = useMountedState();
  const isSliding = useRef(false);
  const frame = useRef(0);
  const [state, setState] = useSetState<State>({
    isSliding: false,
    value: options.default ? options.default : 0
  });

  useEffect(() => {
    if (isClient) {
      const styles = options.styles === undefined ? true : options.styles;
      const reverse = options.reverse === undefined ? false : options.reverse;

      if (ref.current && styles) {
        ref.current.style.userSelect = "none";
      }

      let bindEvents: Function = () => null;
      let unbindEvents: Function = () => null;
      let onMouseMove: Function = () => null;
      let onTouchMove: Function = () => null;
      let onScrub: Function = () => null;

      const startScrubbing = () => {
        if (!isSliding.current && isMounted()) {
          (options.onScrubStart || noop)();
          isSliding.current = true;
          setState({ isSliding: true });
          bindEvents();
        }
      };

      const stopScrubbing = () => {
        if (isSliding.current && isMounted()) {
          (options.onScrubStop || noop)();
          isSliding.current = false;
          setState({ isSliding: false });
          unbindEvents();
        }
      };

      const onMouseDown = (event: MouseEvent) => {
        startScrubbing();
        onMouseMove(event);
      };
      onMouseMove = options.vertical
        ? (event: MouseEvent) => onScrub(event.clientY)
        : (event: MouseEvent) => onScrub(event.clientX);

      const onTouchStart = (event: TouchEvent) => {
        startScrubbing();
        onTouchMove(event);
      };
      onTouchMove = options.vertical
        ? (event: TouchEvent) => onScrub(event.changedTouches[0].clientY)
        : (event: TouchEvent) => onScrub(event.changedTouches[0].clientX);

      bindEvents = () => {
        on(document, "mousemove", onMouseMove);
        on(document, "mouseup", stopScrubbing);

        on(document, "touchmove", onTouchMove);
        on(document, "touchend", stopScrubbing);
      };

      unbindEvents = () => {
        off(document, "mousemove", onMouseMove);
        off(document, "mouseup", stopScrubbing);

        off(document, "touchmove", onTouchMove);
        off(document, "touchend", stopScrubbing);
      };

      onScrub = (clientXY: number) => {
        cancelAnimationFrame(frame.current);

        frame.current = requestAnimationFrame(() => {
          if (isMounted() && ref.current) {
            const rect = ref.current.getBoundingClientRect();
            const pos = options.vertical ? rect.top : rect.left;
            const length = options.vertical ? rect.height : rect.width;

            // Prevent returning 0 when element is hidden by CSS
            if (!length) {
              return;
            }

            let value = (clientXY - pos) / length;

            if (value > 1) {
              value = 1;
            } else if (value < 0) {
              value = 0;
            }

            if (reverse) {
              value = 1 - value;
            }

            setState({
              value
            });

            (options.onScrub || noop)(value);
          }
        });
      };

      on(ref.current, "mousedown", onMouseDown);
      on(ref.current, "touchstart", onTouchStart);

      return () => {
        off(ref.current, "mousedown", onMouseDown);
        off(ref.current, "touchstart", onTouchStart);
      };
    } else {
      return undefined;
    }
  }, [ref, options.vertical]);

  return state;
};

export default useSlider;
