import { useState, useRef } from "react";

/**
 * Useful hook if you want to fullscreen an element of your page.
 * @author Gabe Ragland <> https://github.com/gragland
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @param eventName https://developer.mozilla.org/en-US/docs/Web/Events
 * @param handler Function to perform on event. Should be wrapped in useCallback()
 * @param element Element to attach listener to (default=window)
 * @example
 *  useEventListener('mousemove', handler);
 */
export const useFullscreen = () => {
  const [isFS, setIsFS] = useState(false);
  const elementFS = useRef<any>();

  const triggerFS = () => {
    const el = elementFS.current;

    if (el) {
      el.requestFullscreen && el.requestFullscreen();
      el.mozRequestFullScreen && el.mozRequestFullScreen();
      el.webkitRequestFullscreen && el.webkitRequestFullscreen();
      el.msRequestFullscreen && el.msRequestFullscreen();
    }

    setIsFS(true);
  };

  const exitFS = () => {
    const elFS = elementFS.current.ownerDocument.fullscreen;
    const doc: any = document;
    if (isFS && elFS) {
      doc.exitFullscreen();
      doc.exitFullscreen && doc.exitFullscreen();
      doc.mozCancelFullScreen && doc.mozCancelFullScreen();
      doc.webkitExitFullscreen && doc.webkitExitFullscreen();
      doc.msExitFullscreen && doc.msExitFullscreen();
    }

    setIsFS(false);
  };

  return { elementFS, triggerFS, exitFS, isFS };
};

export default useFullscreen;
